'use client';

import { useCustomerFAQ } from '@/hooks/pelanggan/useCustomerFAQ';
import FAQSearch from '@/components/pelanggan/faq/FAQSearch';
import FAQAccordion from '@/components/pelanggan/faq/FAQAccordion';
import FAQLoading from '@/components/pelanggan/faq/FAQLoading';
import { HelpCircle } from 'lucide-react';

export default function CustomerFAQPage() {
    const { faqs, loading, searchQuery, setSearchQuery } = useCustomerFAQ();

    return (
        <div className="min-h-screen bg-gradient-to-b from-blue-50 to-white">
            {/* Hero Section */}
            <div className="bg-gradient-to-r from-blue-600 to-blue-800 text-white py-12">
                <div className="max-w-4xl mx-auto px-4">
                    <div className="flex items-center gap-3 mb-4">
                        <HelpCircle className="w-8 h-8" />
                        <h1 className="text-4xl font-bold">Pertanyaan yang Sering Diajukan</h1>
                    </div>
                    <p className="text-blue-100 text-lg">
                        Temukan jawaban untuk pertanyaan Anda tentang booking lapangan
                    </p>
                </div>
            </div>

            {/* Content Section */}
            <div className="max-w-4xl mx-auto px-4 py-12">
                {/* Search */}
                <div className="mb-8">
                    <FAQSearch
                        searchQuery={searchQuery}
                        onSearchChange={setSearchQuery}
                    />
                </div>

                {/* FAQ List */}
                {loading ? (
                    <FAQLoading />
                ) : faqs.length === 0 ? (
                    <div className="text-center py-12">
                        <HelpCircle className="w-16 h-16 text-gray-300 mx-auto mb-4" />
                        <p className="text-gray-600 text-lg">
                            {searchQuery
                                ? 'Tidak ada FAQ yang sesuai dengan pencarian Anda'
                                : 'FAQ tidak tersedia saat ini'}
                        </p>
                    </div>
                ) : (
                    <div>
                        <p className="text-sm text-gray-600 mb-6">
                            Menampilkan {faqs.length} pertanyaan
                        </p>
                        <FAQAccordion faqs={faqs} />
                    </div>
                )}
            </div>

            {/* Footer Info */}
            {!loading && faqs.length > 0 && (
                <div className="bg-blue-50 border-t py-8">
                    <div className="max-w-4xl mx-auto px-4 text-center">
                        <p className="text-gray-600 text-sm">
                            Tidak menemukan jawaban yang Anda cari?{' '}
                            <a
                                href="/pelanggan/home"
                                className="text-blue-600 font-semibold hover:underline"
                            >
                                Hubungi tim kami
                            </a>
                        </p>
                    </div>
                </div>
            )}
        </div>
    );
}
